* {

  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}

input{
  font-size: medium;
  width: 100%;
}

.card {
align-content: center;
border: 1px solid grey;
border-radius: 6px;
overflow: hidden;
margin-bottom: 10px;
margin-right: 10px;


}

.card p {

  padding: 5px;
  text-align: center;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}